<template>
    <div>
        <div class="container">
            <el-form ref="filters" :inline="true" :model="filters" class="toolbar">
                <!-- <el-form-item label="车辆品牌">
                    <el-select v-model="filters.car_brand" placeholder="请选择">
                        <el-option
                            v-for="item in optionsCarBrand"
                            :key="item.id"
                            :label="item.bus_brand"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="车辆类型">
                    <el-select v-model="filters.car_class" placeholder="请选择">
                        <el-option
                            v-for="item in optionsCarClass"
                            :key="item.id"
                            :label="item.bus_type"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="驱动形式">
                    <el-select v-model="filters.foreign_model" placeholder="请选择">
                        <el-option
                            v-for="item in optionsModel"
                            :key="item.id"
                            :label="item.bus_series"
                            :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-button @click="handleSearch()">查询</el-button> -->
                <el-button type="primary" icon="plus" @click="$router.push({ path: '/goodsParams/add'})">新建</el-button>
            </el-form>
            <el-table :data="tableData" border v-loading="loading">
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="goods_info" label="商品信息" width="120">
                </el-table-column>
                <el-table-column prop="car_brand" label="车辆品牌" width="120">
                </el-table-column>
                <el-table-column prop="car_series" label="车辆系列" width="120">
                </el-table-column>
                <el-table-column prop="foreign_model" label="公告型号">
                </el-table-column>
                <el-table-column prop="car_class" label="车辆类型">
                </el-table-column>
                </el-table-column>
                <el-table-column prop="fuel_type" label="燃油类型">
                </el-table-column>
                </el-table-column>
                <el-table-column prop="horse_power" label="马力">
                </el-table-column>
                </el-table-column>
                <el-table-column prop="drive_form" label="驱动形式">
                </el-table-column>
                <el-table-column prop="color" label="车身颜色">
                </el-table-column>
                <el-table-column prop="purchase_price" label="采购单价">
                </el-table-column>
                <el-table-column prop="sale_price" label="销售单价">
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <el-button type="text" icon="el-icon-view" @click="$router.push({ path: '/goodsParams/view', query: {id:row.id, operation_uniqid: row.operation_uniqid}})">查看</el-button>
                        <el-button type="text" icon="el-icon-edit" @click="$router.push({ path: '/goodsParams/edit', query: {id:row.id, operation_uniqid: row.operation_uniqid}})">修改</el-button>
                        <el-button type="text" icon="el-icon-delete" @click="handleDelete(row, $index)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :page-size="limit" :total="total">
                </el-pagination>
            </div>
        </div>
    
    </div>
</template>

<script>
    import * as API from '@/api/resourceManagement'
    export default {
        name: 'basetable',
        data() {
            return {
                tableData: [],
                page: 1,
                limit: 10,
                total: 1,
                loading: false,
                filters: {
                    car_brand:'',
                    car_series:'',
                    car_class:'',
                    foreign_model:''
                },
                optionsCarBrand: [],
                optionsCarClass: [],
                optionsModel: [],
            }
        },
        created() {
            this.getData();
            this.getFiltersData();
        },
        computed: {
        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                this.loading = true
                API.apiGoodsParamsList({page: this.page}).then(res => {
                    this.loading = false
                    this.tableData = res.data;
                    this.total = res.total;
                })
            },
            getFiltersData(){
                API.apiGoodsParamsCarType({}).then(res => {
                    this.optionsCarBrand = res.data
                    this.optionsCarClass = res.data
                    this.optionsModel = res.data
                }) 
            },
            handleSearch() {
                this.getData();
            },
            handleDelete(row, index) {
                let params = {
                    id: row.id,
                    operation_uniqid: row.operation_uniqid,
                    status: 2
                }
                API.apiGoodsParamsEdit(params).then(res => {
                    this.tableData.splice(index, 1);
                })
            }
        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
</style>
